.tile {
  position: relative;

  height: 470px;
  width: 350px;
  background-color: #FFFFFF;

  border-radius: 18px;
  -webkit-box-shadow: 0 24px 50px 0 rgba(0,0,0,0.05),0 18px 30px 0 rgba(0,0,0,0.02);
          box-shadow: 0 24px 50px 0 rgba(0,0,0,0.05),0 18px 30px 0 rgba(0,0,0,0.02);

  -webkit-transition: -webkit-transform 0.3s ease-out;

  transition: -webkit-transform 0.3s ease-out;

  -o-transition: transform 0.3s ease-out;

  transition: transform 0.3s ease-out;

  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

@media all and (max-width: 350px) {
  .tile {
    width: 100%;
    height: 400px;
  }
}

.tile:hover {
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px);
  cursor: pointer;
}

.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  -webkit-transition: .3s ease;
  -o-transition: .3s ease;
  transition: .3s ease;
  border-radius: 18px;
  background-color: rgba(255, 255, 255, 0.95);
}

.tile:hover .overlay {
  opacity: 1;
}

.overlay-text {
  font-family: 'Open Sans', sans-serif;
  color: #333333;
  font-size: 28px;
  line-height: 160%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
}

.tile-bgd {
  width: 100%;
  border-radius: 18px 18px 0 0;
}

.tile-bottom {
  position: absolute;
  bottom: 0;

  background-color: white;

  height:200px;
  width: 100%;
}

.tile-text-box {
  position: absolute;
  top: 46px;
  left: 46px;
  margin-right: 46px;
  text-align: left;
}

.tile-header-text {
  font-family: 'Roboto', sans-serif;
  color: #473DD6;
  font-size: 14px;
  font-weight: 700;
}

.tile-title {
  color: #41434f;
  font-family: 'Roboto', sans-serif;
  padding-top: 12px;
  font-size: 28px;
  font-weight: 700;
}

.tile-desc {
  color: #606371;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 14px;
  padding-top: 6px;
  line-height: 150%;

}



